.main {
  display: flex;
  margin-top: 65px;
}

.profile {
  margin-right: 60px;
  width: 30%;
}

.profile__name {
  font-family: Montserrat;
  font-weight: 600;
  font-size: 24px;
  line-height: 26px;
}

.nickname {
  margin: 8px 0 16px;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: -0.5px;
}

.profile__top,
.edit__profile,
.profile__activity--wrapper {
  display: flex;
}

.profile__img {
  margin-right: 75px;
  max-width: 120px;
}

.profile__info {
  margin-left: 20px;
}

.edit__profile {
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  text-decoration-line: underline;
  color: #f05d63;
}

.edit__img {
  width: 20px;
}

.cards {
  width: 70%;
}

.cards__wrapper {
  display: flex;
  flex-wrap: wrap;
}

.dropdown {
  position: relative;
  cursor: pointer;
}

.profile-menu__img {
  width: 18px;
}

.dropdown__body {
  display: none;
  position: absolute;
  z-index: 1;
  top: 41px;
  left: -5px;
  background: #ffffff;
  box-shadow: -15px 0px 100px -20px rgba(204, 204, 204, 1);
  mix-blend-mode: normal;
  opacity: 0.94;
  backdrop-filter: blur(21.7463px);
  border-radius: 8px;
  padding: 23px 0;
  width: 176px;
}
.dropdown__block {
  display: flex;
  padding: 9px 20px;
  cursor: pointer;
}
.dropdown__block:hover {
  background: #ecf1f8;
  mix-blend-mode: normal;
  opacity: 0.6;
  backdrop-filter: blur(54.3656px);
}

.dropdown__img {
  max-width: 16px;
}

.dropdown__link {
  padding-left: 16px;
  color: #3e4056;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
}

.open {
  display: block;
}

.toggle__block {
  display: flex;
  align-items: center;
  margin-bottom: 62px;
  margin-left: 7px;
}

.toggle__status {
  font-size: 16px;
  line-height: 19px;
  font-weight: 600;
}
.toggler {
  position: relative;
  margin: 0 30px;
  width: 40px;
  height: 25px;
  background-color: #ed5368;
  border-radius: 12px;
  transition: 0.5s;
  border: none;
}
.toggler::after {
  position: absolute;
  transition: 0.5s;
  top: 3px;
  left: 18px;
  content: "";
  width: 19px;
  height: 19px;
  background-color: #fff;
  border-radius: 9.5px;
}
.toggler.on {
  background-color: #ace1af;
}
.toggler.on::after {
  left: 3px;
}
.toggler.on:focus {
  outline: none;
}
.toggler:focus {
  outline: none;
}

.profile__activity--wrapper {
  text-align: center;
  margin: 26px 0 20px;
}

.profile__activity {
  font-size: 18px;
  line-height: 24px;
}

.profile__activity:nth-child(2) {
  margin: 0 36px;
}

.activity__title {
  font-weight: bold;
}

.profile__descr {
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
}

@media @medium-widths {
  .main {
    flex-direction: column;
  }
  .profile,
  .cards {
    width: 100%;
  }
  .cards__wrapper {
    justify-content: center;
  }
  .dropdown {
    margin-left: 20px;
    margin-top: 5px;
  }
  .toggle__block {
    justify-content: center;
    margin-top: 62px;
  }
}

