.container {
  width: 1170px;
  padding: 0 20px;
  margin: 0 auto;
}
@media screen and (max-width: 1199px) {
  .container {
    width: 970px;
  }
}
@media screen and (max-width: 991px) {
  .container {
    width: 750px;
  }
}
@media screen and (max-width: 767px) {
  .container {
    width: 100%;
    padding: 0 10px;
  }
}
body {
  background-color: #f2f2f2;
  color: #3e4056;
  min-width: 320px;
  min-height: 100vh;
  font-family: "Open Sans", sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word;
}
img {
  display: block;
  width: 100%;
  height: auto;
}
* {
  box-sizing: border-box;
}
.chromeperfectpixel-overlay {
  width: 100% !important;
  height: 100% !important;
}
.card {
  margin: 5px 10px 15px 0;
  padding: 24px;
  width: 100%;
  max-width: 356px;
  background-color: #fff;
  border-radius: 8px;
}
.card:nth-child(2),
.card:nth-child(4) {
  margin-left: 16px;
}
.card__top {
  font-size: 16px;
  line-height: 19px;
}
.highlighted {
  background-color: #f0f8ff;
  border-radius: 8px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.card__title {
  margin: 8px 0 42px;
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 35px;
  line-height: 41px;
}
.author__photo {
  margin-right: 8px;
  width: 24px;
  height: 24px;
  background-color: #b2b3bb;
  border-radius: 50%;
}
.author__name {
  font-size: 14px;
  line-height: 16px;
}
.card__img {
  margin-right: 8px;
  width: 16px;
}
.card__author,
.card__bottom,
.card__action,
.card__play,
.card__like,
.weblink {
  display: flex;
  align-items: center;
}
.card__author,
.card__img,
.card__action--orange {
  cursor: pointer;
}
.card__bottom {
  justify-content: space-between;
}
.card__action {
  margin-right: -9px;
}
.card__action--orange {
  color: #f05d63;
  font-size: 18px;
  line-height: 25px;
}
.card__play {
  margin-right: 16px;
}
.unvisible {
  visibility: hidden;
}
@media screen and (max-width: 991px) {
  .card,
  .card:nth-child(2),
  .card:nth-child(4) {
    margin: 10px;
  }
}
a {
  text-decoration: none;
}
h1,
h2,
h3,
h4 {
  margin: 0;
  padding: 0;
}
.header {
  position: relative;
  padding-top: 44px;
  background-color: #ffffff;
  box-shadow: 0px 12px 12px rgba(0, 0, 0, 0.06);
}
.header__top--wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.logo__img {
  max-width: 50px;
}
.header__member,
.member,
.actions {
  display: flex;
  align-items: center;
}
.member {
  padding-right: 17px;
}
.member__name {
  font-size: 16px;
  font-style: normal;
  font-weight: normal;
  line-height: 20px;
}
.member__preview {
  padding: 0 12px 0 20px;
}
.member__img {
  max-width: 32px;
}
.dropdown {
  max-width: 16px;
}
.actions__img {
  max-width: 31px;
}
.header__add {
  position: absolute;
  bottom: -32px;
  right: 160px;
  width: 64px;
  height: 64px;
  background: linear-gradient(135deg, #ff974f 0%, #e32c75 100%);
  border-radius: 50%;
}
.header__add::before {
  content: "+";
  padding-left: 17px;
  color: #ffffff;
  font-size: 53px;
}
.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 18px;
  width: 250px;
}
.menu__item {
  padding-bottom: 21px;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: #3e4056;
  opacity: 0.6;
}
.menu__item:hover,
.menu__item:active,
.menu__item:active {
  opacity: 1;
  font-weight: bold;
  box-shadow: 0px -4px 0px 0px rgba(253, 152, 37, 0.73) inset;
}
@media screen and (max-width: 991px) {
  .header {
    padding-bottom: 50px;
  }
  .header__logo {
    padding-bottom: 20px;
  }
  .header__top--wrapper {
    flex-direction: column;
  }
  .header__add {
    right: 0;
    left: 50%;
    transform: translateX(-50%);
  }
  .header__nav {
    justify-content: center;
    width: 100%;
  }
  .menu__item {
    padding: 10px;
  }
}
@media screen and (max-width: 767px) {
  .member__preview {
    padding: 0 10px;
  }
}
.main {
  display: flex;
  margin-top: 65px;
}
.profile {
  margin-right: 60px;
  width: 30%;
}
.profile__name {
  font-family: Montserrat;
  font-weight: 600;
  font-size: 24px;
  line-height: 26px;
}
.nickname {
  margin: 8px 0 16px;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: -0.5px;
}
.profile__top,
.edit__profile,
.profile__activity--wrapper {
  display: flex;
}
.profile__img {
  margin-right: 75px;
  max-width: 120px;
}
.profile__info {
  margin-left: 20px;
}
.edit__profile {
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  text-decoration-line: underline;
  color: #f05d63;
}
.edit__img {
  width: 20px;
}
.cards {
  width: 70%;
}
.cards__wrapper {
  display: flex;
  flex-wrap: wrap;
}
.dropdown {
  position: relative;
  cursor: pointer;
}
.profile-menu__img {
  width: 18px;
}
.dropdown__body {
  display: none;
  position: absolute;
  z-index: 1;
  top: 41px;
  left: -5px;
  background: #ffffff;
  box-shadow: -15px 0px 100px -20px #cccccc;
  mix-blend-mode: normal;
  opacity: 0.94;
  backdrop-filter: blur(21.7463px);
  border-radius: 8px;
  padding: 23px 0;
  width: 176px;
}
.dropdown__block {
  display: flex;
  padding: 9px 20px;
  cursor: pointer;
}
.dropdown__block:hover {
  background: #ecf1f8;
  mix-blend-mode: normal;
  opacity: 0.6;
  backdrop-filter: blur(54.3656px);
}
.dropdown__img {
  max-width: 16px;
}
.dropdown__link {
  padding-left: 16px;
  color: #3e4056;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
}
.open {
  display: block;
}
.toggle__block {
  display: flex;
  align-items: center;
  margin-bottom: 62px;
  margin-left: 7px;
}
.toggle__status {
  font-size: 16px;
  line-height: 19px;
  font-weight: 600;
}
.toggler {
  position: relative;
  margin: 0 30px;
  width: 40px;
  height: 25px;
  background-color: #ed5368;
  border-radius: 12px;
  transition: 0.5s;
  border: none;
}
.toggler::after {
  position: absolute;
  transition: 0.5s;
  top: 3px;
  left: 18px;
  content: "";
  width: 19px;
  height: 19px;
  background-color: #fff;
  border-radius: 9.5px;
}
.toggler.on {
  background-color: #ace1af;
}
.toggler.on::after {
  left: 3px;
}
.toggler.on:focus {
  outline: none;
}
.toggler:focus {
  outline: none;
}
.profile__activity--wrapper {
  text-align: center;
  margin: 26px 0 20px;
}
.profile__activity {
  font-size: 18px;
  line-height: 24px;
}
.profile__activity:nth-child(2) {
  margin: 0 36px;
}
.activity__title {
  font-weight: bold;
}
.profile__descr {
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
}
@media screen and (max-width: 1199px) {
  .main {
    flex-direction: column;
  }
  .profile,
  .cards {
    width: 100%;
  }
  .cards__wrapper {
    justify-content: center;
  }
  .dropdown {
    margin-left: 20px;
    margin-top: 5px;
  }
  .toggle__block {
    justify-content: center;
    margin-top: 62px;
  }
}
