.card {
  margin: 5px 10px 15px 0;
  padding: 24px;
  width: 100%;
  max-width: 356px;
  background-color: #fff;
  border-radius: 8px;
}

.card:nth-child(2),
.card:nth-child(4) {
  margin-left: 16px;
}

.card__top {
  font-size: 16px;
  line-height: 19px;
}

.highlighted {
  background-color: #f0f8ff;
  border-radius: 8px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.card__title {
  margin: 8px 0 42px;
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 35px;
  line-height: 41px;
}

.author__photo {
  margin-right: 8px;
  width: 24px;
  height: 24px;
  background-color: #b2b3bb;
  border-radius: 50%;
}

.author__name {
  font-size: 14px;
  line-height: 16px;
}

.card__img {
  margin-right: 8px;
  width: 16px;
}

.card__author,
.card__bottom,
.card__action,
.card__play,
.card__like,
.weblink {
  display: flex;
  align-items: center;
}

.card__author,
.card__img,
.card__action--orange {
  cursor: pointer;
}

.card__bottom {
  justify-content: space-between;
}

.card__action {
  margin-right: -9px;
}

.card__action--orange {
  color: #f05d63;
  font-size: 18px;
  line-height: 25px;
}

.card__play {
  margin-right: 16px;
}

.unvisible {
  visibility: hidden;
}

@media @small-widths {
  .card,
  .card:nth-child(2),
  .card:nth-child(4) {
    margin: 10px;
  }
}
