body {
  background-color: #f2f2f2;
  color: #3e4056;
  min-width: 320px;
  min-height: 100vh;
  font-family: "Open Sans", sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

* {
  box-sizing: border-box;
}

.chromeperfectpixel-overlay {
  width: 100% !important;
  height: 100% !important;
}
