.header {
  position: relative;
  padding-top: 44px;
  background-color: #ffffff;
  box-shadow: 0px 12px 12px rgba(0, 0, 0, 0.06);
}

.header__top--wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo__img {
  max-width: 50px;
}

.header__member,
.member,
.actions {
  display: flex;
  align-items: center;
}

.member {
  padding-right: 17px;
}

.member__name {
  font-size: 16px;
  font-style: normal;
  font-weight: normal;
  line-height: 20px;
}

.member__preview {
  padding: 0 12px 0 20px;
}

.member__img {
  max-width: 32px;
}

.dropdown {
  max-width: 16px;
}

.actions__img {
  max-width: 31px;
}

.header__add {
  position: absolute;
  bottom: -32px;
  right: 160px;
  width: 64px;
  height: 64px;
  background: linear-gradient(135deg, #ff974f 0%, #e32c75 100%);
  border-radius: 50%;
}

.header__add::before {
  content: "+";
  padding-left: 17px;
  color: #ffffff;
  font-size: 53px;
}

.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 18px;
  width: 250px;
}

.menu__item {
  padding-bottom: 21px;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: #3e4056;
  opacity: 0.6;
}

.menu__item:hover,
.menu__item:active,
.menu__item:active {
  opacity: 1;
  font-weight: bold;
  box-shadow: 0px -4px 0px 0px rgba(253, 152, 37, 0.73) inset;
}

@media @small-widths {
  .header {
    padding-bottom: 50px;
  }
  .header__logo {
    padding-bottom: 20px;
  }
  .header__top--wrapper {
    flex-direction: column;
  }
  .header__add {
    right: 0;
    left: 50%;
    transform: translateX(-50%);
  }
  .header__nav {
    justify-content: center;
    width: 100%;
  }
  .menu__item {
    padding: 10px;
  }
}

@media @extraSmall-widths {
  .member__preview {
    padding: 0 10px;
  }
}
